body {
  background: #eee;
  font-family: 'Nunito SemiBold';
}

#main h2 {
  color: rgb(7, 148, 14);
  text-align: center;
}

#main {
  padding: 0px;
  box-sizing: border-box;
  width: 60%;
  height: 100%;
}

#animal-list {
  padding: 0;
}

#animal-list li {
  display: inline-block;
  margin: 12px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #017941;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0, 3);
  cursor: pointer;
  color: #880e4f;
}
#animal-details {
  position: fixed;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
  background: #d4fcdc;
  padding: 10px;
  overflow: auto;
  box-shadow: -2px -3px 5px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  color: rgb(3, 0, 0);
}

form {
  background: #fff;
  padding: 20 px;

  left: 0;
  bottom: 0;
  width: 400px;
}
form .field {
  display: grid;
  grid-template-columns:
    1fr 1 fr
    grid-gap 10px;
}

form .label {
  text-align: right;
  padding: 6px;
}

form select,
form input {
  margin: 4px;
  padding: 6px;
  box-sizing: border-box;
}
